<template>
<div id="leaveMes">
    <el-table :data="data" border style="width:60.1%" v-loading="load" height="250">
        <el-table-column prop="userNo" label="用户号" width="130"></el-table-column>
        <el-table-column prop="staffName" label="工作人员" width="130"></el-table-column>   
        <el-table-column prop="queueNo" label="预约号码" width="150"></el-table-column>       
        <el-table-column prop="messageType" label="留言星级" width="220">
        </el-table-column>       
        <el-table-column prop="messageContent" label="内容" width="200"></el-table-column>
    </el-table>
</div>
</template>
<script>
export default {
    data(){
        return{
            data:[],
            load:true,
            value:5
        }
    },
    mounted(){
        this.init()
    },
    methods:{
        init(){
            this.axios.getleaveMes({}).then((res)=>{
                if(res.success){
                    this.load =false
                    this.data = res.dataList
                }else{
                    this.$message.error('查询留言信息失败,请检查网络设置')
                }
            })
        },
        deleteWindows(row){
            this.axios.removeLeaveMes({'id':row.id}).then((res)=>{
                if(res.success){
                    this.$message.success(res.message)
                    this.init()
                    return
                }
                this.$message.error('删除失败，请检查网络设置')
            })
        }
    }
}
</script>
<style lang="scss">
#leaveMes{
    .add{
        position: relative;
        top: 100px;
        left: 365px;
    }
    .el-table{
        border:1px solid #000;
        border-radius: 7px 7px 7px 7px;
        box-shadow:0px 0px 3px #000;
        position: relative;
        top:100px;
        left: 150px;
    }
}
</style>

